@import '../../App';

@mixin set-width($form-width) {
	width: $form-width;
}

.card {
	background-color: $main-color;
	display: inline-block;
	margin: 10px;
	padding: 1rem;
	border-radius: 5px;
	box-shadow: 0 2px 8px $shadow-box;
}

.form-card {
	@include set-width(20em);

	position: sticky;
	top: 1em;
	.form-title {
		text-align: center;
	}
}

.form-field {
	width: 20em;
}

.class-card-width {
	width: 11em;
}

.done-card {
	background-color: $main-color;
	width: 15em;
}

.card-title {
	width: 95%;
	text-align: center;
}

.auth-card {
	width: 450px;
	.form-field {
		width: 25em;
	}
}
