@import '../../App';

.auth-form {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: 4em;

    .auth-link {
        display: block;
        color: $background-color;
        margin: 10px auto;
        border: none;
        background-color: inherit;
        cursor: pointer;
    }
    .auth-link:hover {
        color: $background-color-dark;
    }

    .buttons-style {
        margin-top: 10px;
        width: 40%;
    }
}
