@import '../../App';

$board-height: 130px;
$board-min-width: 175px;

.flexbox.card {
    display: flex;
    justify-content: stretch;

    width: 75%;

    overflow: auto;
    height: 75vh;

    margin: 0 auto;
    padding: 15px;

    .group-title {
        display: flex;
        justify-content: center;
    }

    .schedule-day {
        text-align: center;
        font-size: 1.5rem;
        writing-mode: vertical-rl;
        text-orientation: upright;
    }
    .schedule-class {
        min-height: 2 * $board-height;
        text-align: center;
        font-size: 1.5rem;
        writing-mode: vertical-rl;
        text-orientation: upright;
    }

    .delete-item {
        position: absolute;
        right: 15%;
        display: none;
        font-size: 5px;
    }

    .empty-card {
        min-width: 105px;
        min-height: 19px;
    }
    .card.empty-card {
        margin-bottom: 0;
    }
    .card.schedule-class,
    .card.schedule-day {
        margin-top: 4px;
        margin-bottom: 0;
    }
    .tooltip-div {
        position: relative;
        .tooltip-board {
            position: absolute;
            display: none;
            min-width: 100px;
            background-color: #555;
            color: $main-color;
            text-align: justify;
            border-radius: 5px;
            padding: 0 10px;
            margin-left: 20px;
            margin-top: -40px;
            z-index: 1000;
        }

        .tooltip-board::after {
            content: '';
            position: absolute;
            top: 100%;
            left: 50%;
            margin-left: -5px;
            border-width: 5px;
            border-style: solid;
            border-color: #555 transparent transparent transparent;
        }
    }

    .board {
        display: flex;
        justify-content: center;
        min-width: $board-min-width;
        max-height: $board-height;
        min-height: $board-height;
        padding: 5px;

        .card {
            padding: 15px 25px;
            cursor: pointer;
        }
        .card.in-board-card {
            font-size: 9px;
        }
    }
}

.lesson-list.card {
    margin-top: 0;
    min-width: 15%;
    max-width: 15%;
    height: 75vh;
    max-height: 75vh;
    overflow: auto;
    overflow-x: hidden;
    .card {
        min-width: 60%;
        max-width: 60%;
        font-size: 12px;
    }
}

.class-section {
    max-width: 60px;
}

.lesson-board {
    width: 250px;
}
