@import '../../App';

.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0;
    min-height: 50px;

    .navLinks{
        font-size: 22px;
        line-height: 26px;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        color: $main-color;
        padding: 10px;
        background: #3f51b5;

        margin-right: 10px;
        
        &:last-child{
            margin-right: 0;
        }
    }
}
