#teacher_id {
    display: none;
}

.teacher-without-wishes {
    font-size: 22px;
    padding: 10px;
    margin: 10px;
}

.wish-modal {
    position: sticky;
    top: 10px;
}

.day-week-status {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 3px;
    min-width: 135px;
}

.modal-teacher-title {
    max-width: 100%;
    text-align: center;
    margin: 6px;
}

.wish-card {
    min-width: 265px;
    max-width: 800px;
    .wish-title-day {
        margin: 4px 0;
    }
    .wish-title-week {
        margin: 0 0 5px 0;
    }

    .class-status-block {
        display: flex;
        align-items: flex-start;
        padding: 2px;
        ._good {
            color: #028802;
            text-transform: capitalize;
        }
        ._bad {
            color: #fc0606;
            text-transform: capitalize;
        }
        ._ok {
            text-transform: capitalize;
        }
    }
}

.save-block {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.wish-form {
    display: flex;
    flex-direction: column;
    .wish-margin-top {
        margin-top: 8px;
    }
}
