$main-color: #fff;
$background-color: #3f51b5;
$background-color-dark: #32408f;
$delete-color: #e62116;
$shadow-box: #00000042;
$group-background-color: #edefff;
$btn-size: 20px;
$font: Roboto, 'Helvetica Neue', sans-serif;

@mixin flexWrap {
    display: flex;
}

@mixin buttonStyle {
    cursor: pointer;
    width: $btn-size;
    height: $btn-size;
    transition: color 0.3s linear;
}

body {
    margin: 0;
    font-family: $font;
}

.under-line {
    position: relative;

    &::after {
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        bottom: -10px;
        border-bottom: 1px solid;
    }
}

.container {
    max-width: 1440px;
    margin: 10px auto;

    @media (max-width: 1440px) {
        margin: 10px 10px;
    }
}

.form-buttons-container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .buttons-style {
        width: 48%;
    }
}

.margin-top-bottom {
    margin: 2px 0 7px 0;
}

.cards-container {
    @include flexWrap();
    align-items: start;

    .container-flex-wrap {
        @include flexWrap();

        align-items: center;
        justify-content: space-around;
        flex-wrap: wrap;
    }
}

.cards-btns {
    @include flexWrap();
    align-items: center;
    justify-content: flex-end;
    position: relative;
    height: $btn-size;

    .svg-btn {
        @include buttonStyle;
        margin-right: 10px;

        &:last-child {
            margin-right: 0;
        }
    }

    .delete-btn {
        &:hover {
            color: $delete-color;
        }
    }

    .edit-btn {
        &:hover {
            color: $background-color;
        }
    }
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 5px;
}

::-webkit-scrollbar-thumb {
    background: $background-color;
    border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
    background: $background-color-dark;
}

.search-list__panel {
    position: sticky;
    top: 1rem;
    width:30rem;
}