.MuiTableContainer-root {
    max-width: 80em;
    max-height: 40em;
    overflow: scroll;
    position: relative;
    .MuiTable-root {
        thead.MuiTableHead-root
            tr.MuiTableRow-root.MuiTableRow-head
            th.MuiTableCell-root.MuiTableCell-head {
            position: -webkit-sticky;
            position: sticky;
            top: 0;
            width: auto;
            background-color: #fff;
            border: 2px solid #000;
            border-spacing: 0;
            text-align: center;
            font-weight: bold;

            th:first-child {
                left: 0;
                z-index: 3;
                position: -webkit-sticky;
                position: sticky;
            }
        }
        th.MuiTableCell-root.MuiTableCell-head.groupLabelCell {
            border-left: 2px solid #000;
            z-index: 5;
        }

        td.MuiTableCell-root.MuiTableCell-body {
            width: auto;
            padding: 5px;
            z-index: 0;
            position: relative;
            height: 60px;
            font-size: 0.7rem;
            border-collapse: collapse;
        }
        td.MuiTableCell-root.MuiTableCell-body[rowSpan~='2'] {
            height: 120px;
        }

        td.MuiTableCell-root.MuiTableCell-body.dayNameCell,
        td.MuiTableCell-root.MuiTableCell-body.classNameCell {
            border-right: 2px solid #000;
            border-bottom: 2px solid #000;

            padding: 0px;
            text-align: center;
            font-size: 2em;
            position: relative;
            z-index: 1;
            position: -webkit-sticky;
            position: sticky;
            background-color: #fafafa;
        }
        td.MuiTableCell-root.MuiTableCell-body.dayNameCell {
            width: 20px !important;
            left: 0;
            min-width: 50px;
            border-left: 2px solid #000;
            overflow: visible;
            padding: 0px;
            margin: 0px;

            span.dayName {
                display: block;
                position: absolute;
                left: -80%;
                height: 2em;
            }

            span.dayName p {
                transform: rotate(-90deg);
                -webkit-transform: rotate(-90deg);
                -moz-transform: rotate(-90deg);
                -ms-transform: rotate(-90deg);
                -o-transform: rotate(-90deg);
                filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=3);
                padding: 0px;
                margin: 0px;
            }
        }
        td.MuiTableCell-root.MuiTableCell-body.dayNameCell.minHeightDouble {
            height: 240px;
        }
        td.MuiTableCell-root.MuiTableCell-body.dayNameCell.currentDay,
        td.MuiTableCell-root.MuiTableCell-body.classNameCell.currentDay {
            background-color: lightblue;
        }

        td.MuiTableCell-root.MuiTableCell-body.classNameCell {
            width: 10px;
            left: 50px;
        }

        .MuiTableCell-root.MuiTableCell-body.lesson {
            min-width: 150px;
            border: 1px solid #000;
            white-space: pre-wrap;
        }

        .MuiTableCell-root.MuiTableCell-body.MuiTableCell-alignCenter.grouped {
            background-color: #ffc000;
        }

        .MuiTableCell-root.MuiTableCell-body.currentDay {
            background-color: lightblue;
        }
    }
}
